.profile {
  max-width: 210px;
  margin: 5vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  -webkit-box-shadow: 10px 10px 5px 0px rgba(0,0,0,0.75);
  -moz-box-shadow: 10px 10px 5px 0px rgba(0,0,0,0.75);
  box-shadow: 10px 10px 5px 0px rgba(0,0,0,0.75);
  border-radius: 5%;
  border: 2px solid rgba(122, 4, 59, 0.833);
  padding: 10px;
  background-color: rgba(122, 4, 59, 0.1);
}

.description {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  padding: 5px;
}

.avatar {
  display: flex;
  max-width: 50%;
  margin: 15px;
  border-radius: 10%;
  border: 2px solid rgba(122, 4, 59, 0.833);
  background-color: aliceblue;
}

.name {
  font-weight: bold;
  font-size: 20px;
}

.tag,
.location {
  margin: 5px;
  font-size: 16px;
  color: rgb(73, 62, 62);
}

.stats {
  width: 100%;
  display: flex;
  justify-content: space-between;
  margin: 0;
  padding: 0;
  background-color: rgba(122, 4, 59, 0.833);
  border-bottom: 1px solid #5864b4;
  border-radius: 10px;
}

.item {
  display: flex;
  flex-direction: column;
  width: 70px;
  padding: 5px;
  font-size: 12px;
  text-align: center;
  color:aliceblue;
}

.label {
  padding: 5px;
  color: rgb(186, 208, 226);
  font-size: 12px;
  font-weight: 600;
  text-transform: uppercase;
}
.quantity {
  font-weight: bold;
   border-radius: 5px;
}